import { useTranslation } from "react-i18next";
import style from "../AdmissionManager.module.css";
import { useNavigate, useParams } from "react-router-dom";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
const AdmissionPendingOutstandingCard = ({ student, is_applicable }) => {
  const { t } = useTranslation();
  const params = useParams();
  const navigate = useNavigate();
  const onViewApplicantProfile = () => {
    navigate(`/q/${params.username}/member/student/admission/profile`, {
      state: {
        sid: student?._id,
        viewAs: "ADMISSION_ADMIN",
        collectAccess: "ADMISSION_ADMIN",
        govtAccess: "Other",
        cancelAccess: false,
        editStructureControl: "authorized",
        editGovtNumberContorl: "authorized",
        editInstallmentControl: "authorized",
        editGovtInstallmentControl: "authorized",
      },
    });
  };
  return (
    <div
      className={style.apf_container}
      onClick={onViewApplicantProfile}
      title="View Profile"
    >
      <div className={style.apf_container_inner}>
        <img
          alt="Student Avatar"
          src={
            student?.studentProfilePhoto
              ? `${imageShowUrl}/${student?.studentProfilePhoto}`
              : `${assestsFeedUrl}/feed-user-avatar.svg`
          }
          loading="lazy"
        />
        <div className={style.apfci_text}>
          <h6>{`${student.studentFirstName ?? ""} ${
            student.studentMiddleName ? student.studentMiddleName : ""
          } ${student.studentLastName ?? ""}`}</h6>

          <p>
            {t("gr_no")}
            {student?.studentGRNO ?? ""}
            {student?.department?.dName
              ? `, ${student?.department?.dName}`
              : ""}
          </p>
        </div>
      </div>
      <div className={style.apfci_text}>
        {is_applicable ? (
          <h6>
            {t("rs_only")} {student?.applicable_fees_pending}
          </h6>
        ) : (
          <h6>
            {t("rs_only")} {student?.admissionRemainFeeCount}
          </h6>
        )}
        <p>{t("pending_fees")}</p>
      </div>
    </div>
  );
};

export default AdmissionPendingOutstandingCard;
