import { useCallback, useEffect, useState } from "react";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import { useAdmissionPendingFees } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../Loader/Loading";
import QvipleLoader from "../../../Loader/QvipleLoader";
import AdmissionPendingOutstandingCard from "./AdmissionPendingOutstandingCard";

const AdmissionPendingOutstanding = ({ aid }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const { admissionPendingFees, admissionPendingFeesLoading } =
    useAdmissionPendingFees({
      data: {
        aid: aid,
        page: page,
        limit: 10,
        search: search,
        flow: "All_Pending_Fees_Query",
      },
      skip: !aid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionPendingFees?.list?.length) {
      let obj = nextPage(admissionPendingFees?.list?.length);
      setState(() => obj);
    }
  }, [admissionPendingFees?.list]);
  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  // console.info("djkshfjkd", admissionPendingFees, search);
  return (
    <>
      <div
        className={style.fm_search_full}
        style={{
          marginBottom: "0.7rem",
        }}
      >
        <section className={style.fm_search_full_container}>
          <div className={style.fm_search_full_container_inner}>
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
              name="search"
            />
          </div>
        </section>
      </div>

      {admissionPendingFees?.list?.map((student, index) =>
        admissionPendingFees?.list?.length === index + 1 ? (
          <div key={student?._id} ref={ref}>
            <AdmissionPendingOutstandingCard student={student} />
          </div>
        ) : (
          <AdmissionPendingOutstandingCard
            student={student}
            key={student?._id}
          />
        )
      )}

      {admissionPendingFeesLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        admissionPendingFees?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No student found related this search."
          />
        )
      ) : (
        admissionPendingFees?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No student found."
          />
        )
      )}
    </>
  );
};

export default AdmissionPendingOutstanding;
