import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useCallback, useState } from "react";
import PendingFeesSendReminder from "./StudentReminder/PendingFeesSendReminder";
import { useFileUpload } from "../../../Hooks/ApiHooks/Auth/auth-api";
import UploadExcelButton from "../../../Ui/Button/UploadExcelButton";
import { useFinanceStudentFeesMappingExcel } from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
const AdmissionPendingFeesWrapper = ({ aid, fid, instituteId }) => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [open, setOpen] = useState(false);
  const [disabled, setDisabled] = useState(false);

  const [fileUpload] = useFileUpload();
  const [financeStudentFeesMappingExcel] = useFinanceStudentFeesMappingExcel();

  const onNavigate = () => {
    let url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/institute/member/finance?type=reminder-message`
        : `/q/${params.username}/member/staff/finance?type=reminder-message`;
    navigate(url, {
      state: getQuery.state,
    });
  };
  const onAdmissionFeeStatistics = useCallback(() => {
    let url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/institute/member/finance?type=admission-statistics`
        : `/q/${params.username}/member/staff/finance?type=admission-statistics`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, params, getQuery.state]);

  const onToggle = () => {
    setOpen((pre) => !pre);
  };
  const onExcelFileUpload = (e) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey) {
            financeStudentFeesMappingExcel({
              fid: fid,
              excelFile: {
                excel_file: res.data?.imageKey,
              },
            })
              .then(() => {
                setDisabled((pre) => !pre);
              })
              .catch({});
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };
  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("pending_fees")}</h6>
          <div className={style.fm_search}>
            <img
              src={`${assestsUserFinanceUrl}/history.svg`}
              alt="history icon"
              onClick={onNavigate}
            />
            <UploadExcelButton
              onExcelFileUpload={onExcelFileUpload}
              label="fees_mapping"
            />
            <CreateButton
              label="outstanding_reminder"
              customStyle={{
                margin: "0",
                padding: "0.4rem 1rem",
                width: "fit-content",
              }}
              onAction={onToggle}
            />
            <CreateButton
              label="fees_statitics"
              onAction={onAdmissionFeeStatistics}
              customStyle={{
                width: "fit-content",
                margin: "0",
                padding: "0.4rem 1rem",
              }}
            />
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <HorizontalTabWrapper>
        {tabList.map((tab) => (
          <HorizontalTabCard
            key={tab?.id}
            tab={tab}
            isLink
            isActive={tab.groupIs?.includes(searchParams.get("n"))}
            parentState={getQuery.state}
            url={
              getQuery.state?.urlOffSet === "Institute"
                ? `/q/${params.username}/institute/member/finance?type=admission-fee${tab.urlKey}`
                : `/q/${params.username}/member/staff/finance?type=admission-fee${tab.urlKey}`
            }
          />
        ))}
      </HorizontalTabWrapper>
      <BorderBottom
        customStyleBorder={{
          marginTop: "0",
        }}
      />
      {open && (
        <PendingFeesSendReminder
          onClose={onToggle}
          aid={aid}
          fid={fid}
          instituteId={instituteId}
        />
      )}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AdmissionPendingFeesWrapper;

const tabList = [
  {
    label: "all_students",
    id: "student",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "os_fees",
    id: "outstanding",
    urlKey: "&n=outstanding",
    groupIs: ["outstanding"],
  },
  {
    label: "applicable_os",
    id: "applicable",
    urlKey: "&n=applicable",
    groupIs: ["applicable"],
  },
];
